<template>
  <div class="register-box">
    <div class="register-bar"  :style="{backgroundImage: 'url('+imageUrl+')'}">
      <div>{{$t("huanYing")}}<br>{{$t('appName')}}</div>
    </div>
    <div class="open-account">
      <div class="title">{{$t('Slogan')}}</div>
      <div class="upData-input phone-error">
        <van-cell-group>
<!--          <van-field v-model="Phone" type="number" placeholder="请输入手机号" :error-message="inputPhoneError" @input="inputPhone">-->
<!--          </van-field>-->
          <van-field v-model="Phone"  :placeholder="$t('EnterPhone')"  :error-message="inputPhoneError" @input="inputPhone">
          </van-field>
          <van-field type="password" v-model="Password" :placeholder="$t('Password')" :error-message="inputPasswordError" @input="inputPassword">
          </van-field>
          <van-field v-model="InvitationCode" :placeholder="$t('EnterInvitation')">
          </van-field>
<!--          <van-field v-show="codeShow" type="number"  v-model="code" placeholder="请输入短信验证码"  :error-message="inputCodeError" @input="inputCode">-->
<!--            <van-button slot="button" class="code" @click="getyzcode" :disabled="attcode" v-if="showbtn">获取验证码</van-button>-->
<!--            <van-button slot="button" class="code" v-else>{{code_ts}}</van-button>-->
<!--          </van-field>-->
        </van-cell-group>
      </div>
      <div class="upBankCard-but" @click="getLogin">{{$t('logon')}}</div>
      <div class="tips" @click="linkHome">{{$t('HaveAccount')}}，<span style="color: #1988FF">{{$t('SignIn')}}</span></div>
    </div>
  </div>
</template>

<script>
  import { Toast,Dialog } from 'vant';
export default {
  name: 'index',
  data () {
    return {
      // 账号/手机号
      Phone: '',
      // 密码
      Password: '',
      // 邀请码
      InvitationCode: '',
      // 短信验证码
      code: '',
      // 短信验证码登录-验证码输入框显示隐藏
      codeShow: true,
      // 登录方式切换
      loginMode: '验证码登录',
      loginModeOff: false,
      // 是否同意声明
      statement: '',
      confirm: true, // 提交验证按钮判断
      attcode: true, // 点击获取验证码按钮判断
      showbtn: true, // 展示获取验证码或倒计时按钮判断
      yzcode: '',
      code_ts: '获取验证码', // 倒计时提示文字
      sec: 60, // 倒计时秒数
      // 定义websocket
      websock: null,
      // 存储验证码返回字段
      keying: '',
      // 已注册，直接登录
      signIn: false,
      // 盘口类型 盘口URL
      discUrl:'',
      inputPhoneError:'',
      inputPhoneErrorOff:true,
      inputCodeError:'',
      inputCodeErrorOff:true,
      inputPasswordError:'',
      inputPasswordErrorOff:true,
      imageUrl:require('../../assets/loginTop.png'),
    }
  },
  watch: {
    Phone: function (value) {
      let reg = /\b1[3456789]\d{9}\b/g
      let val = reg.test(value)
      if (val) {
        this.attcode = false
      } else {
        this.attcode = true
      }
    }
  },
  created(){
    localStorage.setItem('abnormal','true')
    console.log(this.$store.state.environment)
    console.log(this.$store.state.discUrl)
    this.discUrl = localStorage.getItem('discUrl')
    // this.discUrl = '47.74.187.199'
  },
  // 页面销毁时关闭ws连接
  destroyed () {
    //页面销毁时关闭ws连接
    if(this.websock){
      this.websock.close() // 关闭websocket
    }
  },
  methods: {
    inputPhone(){
      let value = this.Phone
      let reg = /[a-zA-Z0-9_]$/g
      let val = reg.test(value)
      if (val) {
        this.inputPhoneError = ''
        this.inputPhoneErrorOff = true
      } else {
        this.inputPhoneError = this.$t('accountError')
        this.inputPhoneErrorOff = false
      }
    },
    inputCode(){
      let value = this.code
      let reg = /^\d{5}$/g
      let val = reg.test(value)
      console.log(val)
      if (val) {
        this.inputCodeError = ''
      } else {
        this.inputCodeError = '验证码输入有误，请重新确认！'
      }
    },
    inputPassword(){
      let value = this.Password
      let reg = /^ +| +$/g;
      let val = reg.test(value)
      if (!val) {
        this.inputPasswordError = ''
        this.inputPasswordErrorOff = true
      } else {
        this.inputPasswordError = this.$t('inputPasswordError')
        this.inputPasswordErrorOff = false
      }
    },
    // 返回上级
    linkHome () {
      this.$router.go(-1)
    },
    // 获取短信验证码
    // 获取验证码倒计时
    getyzcode () {
      this.$axios.get('http://'+this.discUrl+':7777/api/user/smsregister/' + this.Phone).then(res => {
        console.log('手机号未注册，获取注册验证码')
        if (res.data.code === 0  && res.data.object != null) {
          // 判断短信成功发送
          Toast({
            duration:1000,
            position:'bottom',
            message:'验证码发送成功'
          })
          this.keying = res.data.object.keying
          let timer = setInterval(() => {
            this.sec = this.sec - 1
            this.code_ts = this.sec + 'S后重试'
            this.showbtn = false
            if (this.sec === 0) {
              clearInterval(timer)
              this.sec = 60
              this.code_ts = this.sec + 'S后重试'
              this.showbtn = true
            }
          }, 1000)
        } else {
          if(res.data.msg === '手机号已注册'){
            Toast.fail('手机号已注册请返回登录界面')
          }
        }
      })
    },
    
    getLogin(){
      let that = this
      if(this.inputPhoneErrorOff === true && this.inputPasswordErrorOff === true){
        if (that.global.ws && that.global.ws.readyState === 1) {
          this.global.ws.onopen = this.websocketonopen()
        }
        that.global.ws.onmessage = function(res) {
          that.websocketonmessage (res)
        };
      }else {
        Toast({
          message:that.$t('newMessage'),
          duration:2000,
          position:'bottom',
          onClose () {
            that.Phone = ''
            that.Password = ''
          }
        })
      }
    },

    websocketonopen () { // websocket连接后发送数据(send发送)
      let actions = {
          action: "register",
          reqID: 5,
          data: {
            name: "",
            account: this.Phone,
            password: this.Password,
            paccount: this.InvitationCode,
            // keying: this.keying,
            // code: this.code,
            is_sim: 1
          }
        }
      this.global.ws.send(JSON.stringify(actions))
    },

    websocketonmessage (e) { // 数据接收
      this.redata = JSON.parse(e.data)
      console.log(this.redata)
      let _this = this
      // Toast({
      //   type:'loading',
      //   message:'注册中...',
      //   loadingType: 'spinner',
      // })
      if(this.redata.action ===  "register"){
        if (this.redata.data.code === 0) {
          Toast.clear();
          let account = this.redata.data.msg
          Dialog.confirm({
            title: _this.$t('DialogTitle'),
            showCancelButton:false,
            className:'dialog-class',
            confirmButtonText:_this.$t('confirmButtonText'),
            message: _this.$t('DialogMessage')+_this.Phone
          }).then(() => {
            _this.$router.push({
              name:'signIn',
              params:{
                phone:_this.Phone
              }
            })
          })
        } else {
          let msg = this.redata.data.msg
          let msgTips = ''
          if(msg === '该账户已存在'){
            msgTips = _this.$t('logonF1');
          }else if(msg === '邀请码不存在'){
            msgTips = _this.$t('logonF2');
          }else {
            msgTips = _this.$t('logonF3');
          }
          Toast({
            message:msgTips,
            position:'bottom',
            duration:2000
          })
        }
      }

    },
    websocketsend (Data) { // 数据发送
      this.websock.send(Data)
    },
    websocketclose (e) { // 关闭
      console.log('断开连接', e)
    }
  }
}
</script>

<style scoped>

</style>



